export default "@import '../../../base/style.less';\n@popupPrefix: iot-switch-out;\n\n.@{popupPrefix} {\n  overflow: hidden;\n  width: 100%;\n  height: auto;\n\n  .iot-switch {\n    position: relative;\n    overflow: hidden;\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    margin: var(--card-margin-size);\n    width: var(--card-width-size);\n    height: auto;\n    border: var(--card-border);\n    padding: var(--card-padding-TD);\n    background: var(--card-bg-color);\n    border-radius: var(--card-border-radius-size);\n\n    .switch-left {\n      display: flex;\n      flex-direction: row;\n      align-items: center;\n      flex: var(--card-letf-flex);\n\n      .switch-icon {\n        margin: var(--icon-margin-all);\n        width: var(--icon-width-size);\n        height: var(--icon-height-size);\n        display: flex;\n        flex: 0 0 var(--icon-width-size);\n        flex-direction: row;\n        justify-content: center;\n        align-items: center;\n        border-radius: 50%;\n        background: var(--icon-bg-color);\n\n        .switch-icon-image {\n          display: inline-block;\n          width: var(--icon-inside-width);\n          height: var(--icon-inside-height);\n          font-size: var(--icon-inside-font-size);\n          color: var(--icon-inside-color);\n        }\n      }\n\n      .switch-title {\n        .switch-main {\n          overflow: hidden;\n          font-family: var(--title-font-family);\n          display: -webkit-box;\n          -webkit-line-clamp: 2;\n          -webkit-box-orient: vertical;\n          font-size: var(--title-font-size);\n          color: var(--title-font-color);\n          line-height: var(--title-font-height);\n          font-weight: var(--title-font-weight);\n\n        }\n\n        .switch-sub {\n          overflow: hidden;\n          display: -webkit-box;\n          font-family: var(--subtitle-font-family);\n          -webkit-line-clamp: 2;\n          -webkit-box-orient: vertical;\n          margin-top: var(--subtitle-margin-Top);\n          font-size: var(--subtitle-font-size);\n          color: var(--subtitle-font-color);\n          line-height: var(--subtitle-font-height);\n        }\n\n      }\n    }\n\n    .switch-right {\n      position: relative;\n      flex: var(--card-right-flex);\n      display: flex;\n      justify-content: flex-end;\n\n      /* background-color: yellow; */\n      .switch-btn {\n        transition: all ease .5s;\n        position: relative;\n        display: flex;\n        align-items: center;\n        margin-right: var(--operation-margin-LR);\n        width: var(--switch-width-size);\n        height: var(--switch-height-size);\n        border-radius: var(--switch-round-angle);\n\n        .switch-ball {\n          box-shadow: 0;\n          transition: all ease .5s;\n          position: absolute;\n          width: var(--ball-width-size);\n          height: var(--ball-height-size);\n          background: var(--ball-bg-color);\n          box-shadow: var(--ball-shadow-round);\n          border-radius: 50%;\n        }\n      }\n\n      .switch-btn-on {\n        background: var(--switch-on-bgcolor);\n\n        .switch-ball {\n          right: var(--ball-margin-LR);\n        }\n      }\n\n      .switch-btn-off {\n        background: var(--switch-off-bgcolor);\n\n        .switch-ball {\n          left: var(--ball-margin-LR);\n        }\n      }\n    }\n\n    .switch-disable {\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 100%;\n      z-index: 10;\n      background: var(--disable-bg-color);\n      border-radius: var(--card-border-radius-size);\n    }\n  }\n}";