\n          font-family: var(--subtitle-font-family);\n          line-height: var(--subtitle-font-height);\n          -webkit-line-clamp: 2;\n          -webkit-box-orient: vertical;\n        }\n      }\n\n    }\n\n    .power-right {\n      position: relative;\n      display: flex;\n      flex: var(--card-right-flex);\n      justify-content: flex-end;\n\n      .power-btn-icon {\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n        justify-content: center;\n        width: var(--power-width-size);\n        height: var(--power-height-size);\n        margin-right: var(--operation-margin-LR);\n        border-radius: 50%;\n\n        .power-btn-image {\n          display: inline-block;\n          width: var(--power-icon-width);\n          height: var(--power-icon-height);\n          font-size: var(--power-icon-font-size);\n        }\n      }\n\n      .power-btn-icon-on {\n        color: var(--power-on-color);\n        background: var(--power-on-bgcolor);\n        border: var(--power-on-border);\n\n      }\n\n      .power-btn-icon-off {\n        color: var(--power-off-color);\n        background: var(--power-off-bgcolor);\n        border: var(--power-off-border);\n      }\n\n    }\n\n    .power-disable {\n      position: absolute;\n      top: 0;\n      left: 0;\n      z-index: 10;\n      width: 100%;\n      height: 100%;\n      background: var(--disable-bg-color);\n      border-radius: var(--card-border-radius-size);\n    }\n\n    // .title-wrap{\n    //   display: flex;\n    //   flex-direction: column;\n    //   justify-content: center;\n    //   .title{\n    //     color: @title-color;\n    //     font-size: @title-font-size;\n    //   }\n    //   .subTitle{\n    //     margin-top: 8px;\n    //     color: @subtitle-color;\n    //     font-size: @subtitle-font-size;\n    //   }\n    // }\n  }\n}";